@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,h6{margin:0}.app{font-family:"Montserrat",sans-serif;margin:60px 20px}.app .titleContainer{text-align:center}.app .titleContainer .title{font-weight:600}.app .titleContainer .title .dot{color:#2684ff}.app .inputContainer{margin-bottom:40px}.app .inputContainer .inputTitle{margin:25px 0 10px 0}.app .outputContainer{margin:0 0 40px 0}.app .outputContainer .grossContainer,.app .outputContainer .netContainer{color:#fff;background:#2684ff;text-align:center;border-radius:4px;min-width:100px;padding:20px 40px;margin:0 0 20px 0}.app .outputContainer h4{margin-bottom:10px}.app .outputTable{background:#f0f0f0;border-radius:4px;padding:20px 20px}.app .outputTable .container{justify-content:space-between;border-bottom:1px solid #b7b7b7}.app .outputTable .container .text{flex:2 1;text-align:end;margin:10px 0}.app .outputTable .container .color{color:#2684ff}.app .outputTable .container .percent{flex:2 1;text-align:end;margin:10px 0}.app .outputTable .container .number{flex:1 1;text-align:end;margin:10px 0}.input{font-size:16px;font-family:"Montserrat",sans-serif;color:#333;background-color:#fff;border-color:#ccc;border-radius:4px;border-style:solid;border-width:1px;min-height:38px;box-sizing:border-box;flex-wrap:wrap;outline:0;padding:2px 8px;overflow:hidden;width:100%}.input:focus{border-color:#2684ff;border-width:2px}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}@media screen and (min-width: 768px){.app{margin:60px 80px}.app .outputContainer{display:flex;justify-content:center}.app .outputContainer .grossContainer,.app .outputContainer .netContainer{margin:0 20px}.app .outputTable .container{display:flex}.app .outputTable .container .text{text-align:start}.app .outputTable .container .percent{text-align:center}.app .outputTable .container .number{text-align:end}}@media screen and (min-width: 1024px){.app{max-width:1024px;margin:60px auto}.app .inputContainer .left,.app .inputContainer .right,.app .inputContainer .bottom{display:flex}.app .inputContainer .left .amount{flex:1 1;margin-right:20px}.app .inputContainer .left .residence{flex:1 1;margin-left:20px}.app .inputContainer .right .children{flex:1 1;margin-right:20px}.app .inputContainer .right .dependants{flex:1 1;margin-left:20px}.app .inputContainer .bottom .invalidity{flex:1 1;margin-right:20px}.app .inputContainer .bottom .type{flex:1 1;margin-left:20px}}
